<template>
  <div class="main__background">
    <div class="main__wrapper">
      <div class="book__main">
        <div class="book__mainest">
          <div class="book__wrapper" :class="{ active: wrapperFlag }">
            <aside-component @coordsToMain="coordHoisting"> </aside-component>
          </div>
          <div class="book__second">
            <div
              class="book__seca"
              :class="{ active: wrapperFlag }"
              @click="wrapper()"
            >
              <h1 class="book__title" v-if="!wrapperFlag">
                Красная книга Свердловской области
              </h1>
            </div>
            <map-component :coordsToMap="objectCoords"> </map-component>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import asideComponent from "./aside-component.vue";
import mapComponent from "./map-component.vue";

export default {
  components: { asideComponent, mapComponent },
  name: "MainPage",
  data() {
    return {
      objectCoords: {},
      wrappist: false,
      wrapperFlag: false,
    };
  },
  methods: {
    coordHoisting(value) {
      this.objectCoords = value;
    },
    wrapper() {
      this.wrapperFlag = !this.wrapperFlag;
      setTimeout(() => {
        this.wrappist = !this.wrappist;
      }, 2000);
    },
  },
  watch: {
    objectCoords() {
      return this.objectCoords;
    },
  },
};
</script>

<style lang="scss">
@font-face {
  font-family: "Old";
  src: url("../../assets/old.ttf") format("truetype");
}
.main__background {
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  background: url("../../assets/back.png");
}
.main__wrapper {
  margin: 0 auto;
  width: 70vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.book__main {
  perspective: 2000px;
  width: 70vw;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.book__mainest {
  transform: rotateX(20deg);
  display: flex;
  justify-content: center;
  align-items: center;
}
.book__wrapper {
  z-index: 12;
  width: 40%;
  height: 750px;
  visibility: hidden;
  transition: opacity 1s linear;
  opacity: 0;
}

.book__wrapper.active {
  transition-delay: 1.5s;
  visibility: visible;
  opacity: 1;
}
.book__second {
  perspective: 2000px;
  z-index: 11;
  height: 750px;
  width: 40%;
}
.book__seca {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 12;
  position: absolute;
  width: 103%;
  height: 750px;
  background: url("../../assets/back2.jpg") center;
  background-size: cover;
  transform-origin: left;
  transition: transform 1s linear, box-shadow 0.3s linear;
}
.book__seca:hover {
}
.book__seca.active {
  transform-origin: left;
  transform: rotateY(-180deg);
  z-index: 13;
  animation-name: book;
  animation-duration: 0.6s;
}
@keyframes book {
  0% {
    box-shadow: 50px 2px 6px -5px rgba(34, 60, 80, 0.7);
  }
  100% {
    box-shadow: 50px 14px 6px 7px rgba(7, 12, 15, 0.5);
  }
}
.book__title {
  text-align: center;
  width: 200px;
  color: #f0e68c;
  font-family: "Old";
}

@media (max-width: 1024px) {
  .main__wrapper {
    width: 90vw;
    height: 100vh;
  }
  .book__main {
    height: 70vh;
  }
  .book__mainest {
    height: 70vh;
  }
  .book__wrapper {
    height: 70vh;
  }
  .book__second {
    height: 70vh;
  }
  .book__seca {
    height: 70vh;
  }
}
@media (max-width: 768px) {
  .main__wrapper {
    display: block;
    width: 80vw;
    height: 100vh;
  }
  .book__main {
    margin: 0;
    padding: 0;
    height: 70vh;
    display: block;
    perspective: none;
  }
  .book__mainest {
    height: 70vh;
    width: 80vw;
    display: block;
  }
  .book__wrapper {
    height: 100vh;
    width: 80vw;
    position: absolute;
  }
  .book__second {
    height: 100vh;
    width: 80vw;
  }
  .book__seca {
    height: 100vh;
    width: 80vw;
  }
}
@media (max-width: 360px) {
  .main__wrapper {
    display: block;
    width: 80vw;
    height: 90vh;
  }
  .book__main {
    margin: 0;
    padding: 0;
    height: 60vh;
    display: block;
    perspective: none;
  }
  .book__mainest {
    height: 60vh;
    width: 80vw;
    display: block;
  }
  .book__wrapper {
    height: 90vh;
    width: 80vw;
    position: absolute;
  }
  .book__second {
    height: 90vh;
    width: 80vw;
  }
  .book__seca {
    height: 90vh;
    width: 80vw;
  }
}
</style>
