<template>
  <div class="main__aside">
    <h1 class="main__title">Красная книга Свердловской области</h1>

    <router-view
      @clicked="hoistingCoordsToMain"
      @destroy="destr"
      v-slot="{ Component }"
    >
      <transition name="fade" mode="out-in">
        <component :is="Component" />
      </transition>
    </router-view>
  </div>
</template>

<script>
export default {
  methods: {
    hoistingCoordsToMain(value) {
      this.$emit("coordsToMain", value);
    },
    destr() {
      this.$emit("coordsToMain", null);
    },
  },
  emits: {
    clicked: null,
    destroy: null,
    coordsToMain: null,
  },
};
</script>

