import { createRouter, createWebHistory } from 'vue-router'
import MainContent from '../components/main-page/main-content.vue'

const routes = [
  {
    path: '/',
    name: 'MainContent',
    component: MainContent,
  },
  {
    path: '/animals',
    name: 'Animals',
    component: () => import('../components/species/animals/animals-data.vue'),
  },
  {
    path: '/birds',
    name: 'Birds',
    component: () => import('../components/species/birds/birds-data.vue'),
  },
  {
    path: '/amphibia',
    name: 'Amphibia',
    component: () => import('../components/species/amphibia/amphibia-data.vue'),
  },
  {
    path: '/fishes',
    name: 'Fishes',
    component: () => import('../components/species/fishes/fishes-data.vue'),
  },
  {
    path: '/insects',
    name: 'Insects',
    component: () => import('../components/species/insects/insects-data.vue'),
  },
  {
    path: '/reptiles',
    name: 'Reptiles',
    component: () => import('../components/species/reptiles/reptiles-data.vue'),
  },
  {
    path: '/species/:itemRout',
    name: "SpeciesPage",
    component: () => import('../components/species/species-page.vue'),
  },
  {
    path: '/:pathMatch(.*)*',
    name: "NotFound",
    component: () => import('../components/page-not-found.vue'),
  },


]

const router = createRouter({
  history: createWebHistory('/red-book/'),
  routes
})

export default router
