<template>
  <div class="main__map">
    <template v-if="coords">
      <div class="point__info">{{ coords.status }}</div>
      <div
        class="points"
        :style="item"
        v-for="item in getCoordinates"
        :key="item"
      ></div>
      <div class="ekb">Екатеринбург</div>
      <div class="tagil">Нижний Тагил</div>
    </template>
    <img class="map__image" src="../../assets/map.png" alt="" />
  </div>
</template>

<script>
export default {
  props: ["coordsToMap"],
  data() {
    return {
      coords: null,
    };
  },
  computed: {
    getCoordinates() {
      return this.coords.coordinates;
    },
  },

  watch: {
    coordsToMap() {
      if (this.coordsToMap !== {}) {
        this.coords = this.coordsToMap;
      }
    },
  },
};
</script>

<style lang="scss">
.main__map {
  border: 3px solid #ffefd5;
  // position: relative;
  // width: 40%;
  height: 100%;
}
.map__image {
  height: 100%;
  width: 100%;
  display: block;
}
.points {
  cursor: pointer;
  transform-origin: center;
  z-index: 11;
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #dc143c;
  transition: transform 0.5s;
  animation-name: point;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
}
.points::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transform: translateX(-17%) translateY(-17%);
  border: 3px solid #dc143c;
  opacity: 0;
  animation-name: pulse;
  animation-duration: 0.75s;
  animation-iteration-count: infinite;
}
@keyframes pulse {
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes point {
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}
.point__info {
  border: 3px solid rgb(109, 9, 9);
  font-weight: 700;
  font-size: 20px;
  color: rgb(109, 9, 9);
  position: absolute;
  right: 0;
  background-size: cover;
  height: 50px;
  width: 150px;
  padding: 30px 10px;
  text-align: center;
  z-index: 9;
}
@media (max-width: 1024px) {
  .points {
    width: 15px;
    height: 15px;
  }
}
.ekb {
  transform-origin: center;
  z-index: 10;
  position: absolute;
  left: 35%;
  bottom: 12%;
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: blue;
  transition: transform 0.5s;
}
.tagil {
  transform-origin: center;
  z-index: 10;
  position: absolute;
  left: 28%;
  bottom: 28%;
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: blue;
  transition: transform 0.5s;
}
</style>
